.buying {
  position: relative;
  &:before {
    content: "";
    width: 100%;
    height: 710px;
    position: absolute;
    top: 0;
    right: 0;
    background: url("../../img/gradient.png") center / cover no-repeat;
  }
}

.buying-hero {
  margin-bottom: 110px;
  position: relative;

  .buying-hero-content {
    border-radius: 20px;
    background: var(--white) url("../../img/buying-bg.webp") center / cover
      no-repeat;
    padding: 70px 20px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(27px, 3vw, 45px);
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: var(--gray);
  }

  .buying-hero-content-inner {
    max-width: 585px;
    margin: 0 auto;
  }

  p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: var(--gray);
    opacity: 0.5;
    margin-bottom: 33px;
  }

  &__actions {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 13px;
  }

  &__btn {
    color: var(--white);
    background: var(--accent);
    padding-block: 12px;

    &--cb {
      background: #c5d1ff;
      color: var(--accent);
      @media (any-hover: hover) {
        &:hover {
          color: var(--white);
          background: var(--hovered);
        }
      }
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--white);
        background: var(--hovered);
      }
    }
  }
}

.proccess {
  margin-bottom: 100px;
  &__title {
    margin-bottom: 40px;
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    row-gap: 35px;
  }

  &__cart {
    display: flex;
    flex-direction: column;
    border-radius: clamp(10px, 2vw, 20px);
    padding: 22px;
    background: #f8f8f8;

    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 25px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
      margin-top: 24px;
    }

    &:last-child {
      background: var(--accent);

      .proccess__cart-title {
        color: var(--white);
        margin-bottom: 18px;
        margin-top: 0;
      }
      .proccess__cart-desc {
        color: var(--white);
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 97%;
        letter-spacing: -0.01em;
        flex: 1 1 auto;
      }
    }
    &-btn {
      background: var(--white);
      padding-block: 21px;
      border-radius: 5px;
      color: var(--gray);

      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
          color: var(--white);
        }
      }
      &:active {
        background: var(--active);
        color: var(--white);
      }
    }
  }
}

.filter-franch {
  margin-bottom: 100px;
  .content {
    border-radius: 10px;
    padding: 28px 21px;
    background: #f8f8f8;
  }
  .row {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 15px;
    margin-bottom: 35px;
    .btn {
      background: var(--accent);
      padding-block: 21px;
      color: var(--white);
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
          color: var(--white);
        }
      }
    }

    .ss-main {
      padding-block: 21px;
      background: var(--white);
      border-radius: 10px;
      padding-inline: 14px;
    }
  }

  .tags {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;

    .tag {
      background: var(--blue);
      max-width: fit-content;
      border-radius: 100px;
      padding: 10px 22px;
      color: var(--accent);
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
    }
  }
}
